
import { defineComponent } from "vue";

export default defineComponent({
  emits: ["change"],
  data() {
    return {
      visibility: false,
    };
  },

  watch: {
    visibility(visible: boolean) {
      const type = visible ? "text" : "password";
      this.$emit("change", type);
    },
  },

  methods: {
    changeVisibility() {
      this.visibility = !this.visibility;
    },
  },
});
